import React from 'react'

export default class Icon extends React.Component {
    getClassName() {
        return 'fa fa-' + this.props.name + (this.props.fw ? ' fa-fw' : '')
    }

    render() {
        return (
            this.props.name ? <i className={ this.getClassName() }></i> : null
        )
    }
}



