import React from 'react'
import { connect } from 'react-redux'

export class Pane extends React.Component {
    render() {
        return (
            <div className={ 'pane' + (this.props.active ? ' active' : '') }>
                <h4>{ this.props.title }</h4>
            </div>
        )
    }
}

export class Panes extends React.Component {
    render() {
        return (
            <div id={ this.props.id }>
                { this.props.panes.map(pane => <Pane key={ pane.id } { ...pane } />) }
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        panes: state.panes.panes
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        // onItemClick: (id) => {
        //     dispatch(actions.selectItem(id))
        // }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Panes)



