import React from 'react'
import Icon from './font-awesome'
import { connect } from 'react-redux'
import * as actions from '../actions/dock'

export class Item extends React.Component {
    render() {
        return (
            <li className={ this.props.active ? 'active' : null } onClick={ this.props.onClick } >
                <div>
                    <Icon name={ this.props.icon } fw={ true } />
                    <span>{ this.props.label }</span>
                </div>
            </li>
        )
    }
}

export class Dock extends React.Component {
    render() {
        return (
            <ul id={ this.props.id }>
                { this.props.items.map(item => <Item key={ item.id } onClick={ () => this.props.onItemClick(item.id) } { ...item } />) }
            </ul>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        items: state.dock.items
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onItemClick: (id) => {
            dispatch(actions.selectItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dock)



