import React from 'react'
import * as panesActions from '../actions/panes'
import * as dockActions from '../actions/dock'

let nextPaneId = 0

function addPane(panes, pane) {
    return [...panes, Object.assign({}, pane, {
        id: nextPaneId++,
        active: pane.active || false
    })]
}

function removePane(panes, id) {
    return panes
}

function selectPane(panes, id) {
    return panes.map(
        pane => Object.assign({}, pane, { active: id === pane.id })
    )
}

function handlePanes(panes = [], action) {
    switch (action.type) {
        case panesActions.ADD_PANE:
            return addPane(panes, action.payload)
        case panesActions.REMOVE_PANE:
            return removePane(panes, action.payload)
        case panesActions.SELECT_PANE:
            return selectPane(panes, action.payload)
        case dockActions.SELECT_ITEM:
            return selectPane(panes, action.payload)
        default:
            return panes
    }
}

export default function panes(state = {}, action) {
    return {
        panes: handlePanes(state.panes, action)
    }
}



