export const ADD_PANE    = 'ADD_PANE'
export const REMOVE_PANE = 'REMOVE_PANE'
export const SELECT_PANE = 'SELECT_PANE'

export function addPane(pane) {
    return { type: ADD_PANE, payload: pane }
}

export function removePane(id) {
    return { type: REMOVE_PANE, payload: id }
}

export function selectPane(id) {
    return { type: SELECT_PANE, payload: id }
}



