import React from 'react'
import * as actions from '../actions/dock'

let nextItemId = 0

function addItem(items, item) {
    return [...items, Object.assign({}, item, {
        id: nextItemId++,
        active: item.active || false
    })]
}

function removeItem(items, id) {
    return items
}

function selectItem(items, id) {
    return items.map(
        item => Object.assign({}, item, { active: id === item.id })
    )
}

function handleItems(items = [], action) {
    switch (action.type) {
        case actions.ADD_ITEM:
            return addItem(items, action.payload)
        case actions.REMOVE_ITEM:
            return removeItem(items, action.payload)
        case actions.SELECT_ITEM:
            return selectItem(items, action.payload)
        default:
            return items
    }
}

export default function dock(state = {}, action) {
    return {
        items: handleItems(state.items, action)
    }
}



