import React from 'react'
import { createStore } from 'redux'
import { Provider } from 'react-redux'
import reducers from '../reducers'
import Dock from './dock'
import Panes from './panes'
import Workspace from './workspace'
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.min.css'
import '../styles/index.css'

let store = createStore(reducers)

import * as dockActions from '../actions/dock'
import * as panesActions from '../actions/panes'

export default class LaserWeb extends React.Component {
    constructor(props) {
        super()

        store.dispatch(dockActions.addItem({ icon: 'question', label: 'Arrow1' }));
        store.dispatch(dockActions.addItem({ icon: 'question', label: 'Arrow2', active: true }));
        store.dispatch(dockActions.addItem({ icon: 'question', label: 'Arrow3' }));

        store.dispatch(panesActions.addPane({ title: 'Arrow1' }));
        store.dispatch(panesActions.addPane({ title: 'Arrow2', active: true }));
        store.dispatch(panesActions.addPane({ title: 'Arrow3' }));
    }

    render() {
        return (
            <Provider store={store}>
                <div id="root">
                    <Dock id='dock' />
                    <Panes id='panes' />
                    <Workspace id='workspace' />
                </div>
            </Provider>
        )
    }
}



